(function() {
  'use strict';
  var React = require('react/addons'),
      _ = require('underscore'),
      ModelMixin = require('../../vendor/backbonemixin.js'),
      LoadingOverlay = require('./overlays.js').Activity,
      ProgressOverlay = require('./overlays.js').Progress,
      foundation = require('./foundation.js'),
      util = require('../util.js'),
      row = foundation.row,
      column = foundation.column,
      modal = foundation.modal,
      confirmModal = foundation.confirmModal,
      ActionBar, WorkflowItem;

  ActionBar = React.createClass({displayName: 'ActionBar',
    getInitialState: function() {
      return {
        actionDropdownVisible: false,
        dropdownWidth: undefined
      };
    },
    toggleActionDropdown: function() {
      this.setState({actionDropdownVisible: !this.state.actionDropdownVisible});
    },
    componentDidUpdate: function() {
      if (this.state.actionDropdownVisible) {
        var domNode = this.getDOMNode();
        var dropdownWidth = this.getDOMNode().getElementsByClassName('action-select')[0]
                            .getBoundingClientRect().width;
        domNode.getElementsByClassName('button-list')[0].style.width = dropdownWidth + "px";
      }
    },
    render: function() {
      return (
        row(null, 
          React.DOM.div( {className:"small-6 medium-12 columns"}, 
            this.props.smallDisplay &&
              React.DOM.a( {onClick:this.toggleActionDropdown, className:"action-select action-button small dropdown fi-list",
                  title:"View actions"}, "Actions"),
            
            (!this.props.smallDisplay || this.state.actionDropdownVisible) &&
            React.DOM.ul( {className:this.props.smallDisplay ? "button-list": "button-group"}, 
              React.DOM.li(null, 
                React.DOM.a( {title:"Edit the workflow",
                    href:'/workflow/' + this.props.workflowId + '/edit',
                    className:"action-button small fi-pencil"}, 
                    this.props.smallDisplay && " Edit"
                )
              ),
              React.DOM.li(null, 
                React.DOM.a( {onClick:this.props.removalBlocked ? null : this.props.handleRemove,
                    title:"Remove workflow and all associated files",
                    className:"action-button small fi-trash" + (this.props.removalBlocked ? " disabled" : "")}, 
                    this.props.smallDisplay && " Remove"
                )
              ),
              React.DOM.li(null, 
                React.DOM.a( {'data-bypass':true,
                    title:"Download workflow as a ZIP archive",
                    onClick:this.props.handleDownload,
                    href:'/api/workflow/' + this.props.workflowId + '/download',
                    className:"action-button small fi-download"}, 
                  this.props.smallDisplay && " Download"
                )
              ),
              window.config.web.mode !== 'postprocessor' &&
                React.DOM.li(null, 
                  React.DOM.a( {onClick:this.props.handleCapture,
                      title:"Capture images",
                      className:"action-button small fi-camera"}, 
                  this.props.smallDisplay && " Capture"
                )
                ),
              window.config.web.standalone_device &&
                React.DOM.li(null, 
                  React.DOM.a( {onClick:this.props.handleTransfer,
                      title:"Transfer workflow directory to a removable storage device",
                      className:"action-button small fi-usb"}, 
                    this.props.smallDisplay && " Transfer"
                  )
                )
            )
          )
        )
      );
    }
  })

  /**
   * Display a single workflow with thumbnail, metadata and available actions.
   *
   * @property {Workflow} workflow  - Workflow to set configuration for
   */
  WorkflowItem = React.createClass({displayName: 'WorkflowItem',
    getInitialState: function() {
      return {
        /** Display deletion confirmation modal? */
        deleteModal: false,
        downloadWaiting: false,
        downloadInProgress: false,
        transferWaiting: false,
        transferProgress: 0,
        transferCurrentFile: undefined,
        stepProgress: 0
      };
    },
    /**
     * Remove associated workflow object from the model collection.
     */
    doRemove: function() {
      this.props.workflow.destroy();
      // Disable deletion confirmation modal
      this.setState({
        deleteModal: false
      });
    },
    /**
     * Enable deletion confirmation modal
     */
    handleRemove: function() {
      this.setState({
        deleteModal: true
      });
    },
    /**
     * Continue to next step in workflow.
     */
    handleCapture: function() {
      window.router.navigate('/workflow/' + this.props.workflow.id + '/capture',
                             {trigger: true});
    },
    /**
     * Tries to initiate transfer of associated workflow to an external
     * storage device. Displays an error modal if something goes wrong,
     * otherwise displays a loading overlay as long as the transfer is not
     * completed.
     */
    handleTransfer:  function() {
      this.props.workflow.transfer(function(xhr, status) {
        if (status !== 'success') {
          var data = xhr.responseJSON,
              errorText;
          if (data && data.error) {
            errorText = data.error;
          } else {
            errorText = "Check the server logs for details";
          }
          // Display error modal
          this.setState({
            errorModal: true,
            errorModalHeading: "Transfer failed",
            errorModalText: errorText
          });
        } else {
          // Enable loading overlay
          this.setState({
            transferWaiting: true,
            transferProgress: 0,
            transferCurrentFile: undefined
          });
          // Bind progress events
          window.router.events.on('transfer:progressed', function(data) {
            if (this.isMounted()) {
              this.setState({
                transferProgress: data.progress*100 | 0,
                transferCurrentFile: data.status
              });
            }
          }.bind(this));
          // Register callback for when the transfer is completed
          window.router.events.on('transfer:completed', function() {
            // Disable loading overlay
            this.setState({
              transferWaiting: false
            });
          }.bind(this));
        }
      }.bind(this));
    },
    handleDownload: function() {
      this.setState({
        downloadWaiting: true,
        downloadInProgress: true
      });
      window.router.events.on('download:prepare-progressed', function() {
          this.setState({downloadPrepareProgress: data.progress*100 | 0,
                         downloadPrepareCurrentFile: data.status});
      }, this);
      window.router.events.on('download:prepared', function() {
        this.setState({downloadWaiting: false});
      }, this);
      window.router.events.on('download:finished', function() {
        this.setState({downloadInProgress: false});
      }, this);
    },
    render: function() {
      var workflow = this.props.workflow,
          workflowUrl = '/workflow/' + workflow.get('id'),
          removalBlocked = (this.state.downloadInProgress || this.state.transferWaiting),
          actionBar;

      actionBar = (ActionBar( {workflowId:workflow.id,
                              handleRemove:this.handleRemove,
                              handleDownload:this.handleDownload,
                              handleCapture:this.handleCapture,
                              handleTransfer:this.handleTransfer,
                              removalBlocked:removalBlocked,
                              smallDisplay:this.props.smallDisplay}));
      return (
        React.DOM.div(null, 
          row(null, 
            /* Display waiting for download overlay? */
            this.state.downloadWaiting &&
              ProgressOverlay( {progress:this.state.downloadPrepareProgress,
                              statusMessage:this.state.downloadPrepareCurrentFile || "Preparing download..."}),
            
            /* Display deletion confirmation modal? */
            this.state.deleteModal &&
              confirmModal(
                {onCancel:function(){this.setState({deleteModal: false});}.bind(this),
                onConfirm:this.doRemove, fixed:true}, 
                React.DOM.h1(null, "Remove?"),
                React.DOM.p(null, "Do you really want to permanently remove this workflow and all"+' '+
                  "of its related files?")
              ),
            /* Display error modal? */
            this.state.errorModal &&
              modal( {onClose:function(){this.setState({errorModal: false});}.bind(this),
                    fixed:true}, 
                React.DOM.h1(null, this.state.errorModalHeading),
                React.DOM.p(null, this.state.errorModalText)
              ),
            /* Display loading overlay */
            this.state.transferWaiting &&
              ProgressOverlay( {progress:this.state.transferProgress,
                              statusMessage:this.state.transferCurrentFile || "Preparing transfer..."}),
            /* Display preview image (second-to last page) if there are images
                in the workflow */
            column( {size:[6, 4]}, 
            workflow.get('images').length > 0 ?
              React.DOM.a( {href:workflowUrl}, 
                React.DOM.img( {width:"100%", src:workflow.get('images').slice(-2)[0] + '/thumb'} )
              ):
              'no images'
            
            ),
            column( {size:[6, 8]}, 
              row(null, 
                column(null, 
                  React.DOM.h3(null, React.DOM.a( {title:"View details",
                      href:workflowUrl}, workflow.get('name')))
                )
              ),
              row(null, 
                column(null, 
                  React.DOM.p(null, workflow.has('images') ? workflow.get('images').length : 0, " pages")
                )
              ),
              _.contains(["process", "output"], workflow.get('step')) &&
              row(null, 
                React.DOM.p(null)
              ),
              !this.props.smallDisplay && actionBar
            )
          ),
          this.props.smallDisplay && actionBar
        )
      );
    }
  });

  /**
   * Container component that holds all WorkflowItems
   *
   * @property {Backbone.Collection<Workflow>} workflows
   */
  module.exports = React.createClass({
    displayName: "WorkflowList",

    /** Enables two-way databinding with Backbone model */
    mixins: [ModelMixin],

    /** Activates databinding for `workflows` model collection property. */
    getBackboneModels: function() {
      return this.props.workflows;
    },
    getInitialState: function() {
      return { mqSmall: util.isSmall() };
    },
    componentWillMount: function() {
      matchMedia(util.mediaQueries.medium).addListener(function(mql){
        this.setState({ mqSmall: !mql.matches});
      }.bind(this));
    },
    render: function() {
      return(
        React.DOM.main(null, 
          row(null, 
            column( {size:"18"}, 
              React.DOM.h1(null, "Workflows")
            )
          ),
          React.DOM.div(null, 
            this.props.workflows.length > 0 ?
              this.props.workflows.map(function(workflow) {
                return WorkflowItem( {key:workflow.id, workflow:workflow,
                                     smallDisplay:this.state.mqSmall});
              }, this):
              row(null, 
                column(null, React.DOM.h2(null, "No workflows yet!"),
                React.DOM.p(null, 
                  "Once you have scanned a book, you can see it (and all"+' '+
                  "other books you have scanned so far) and do the following"+' '+
                  "things with it:",
                  React.DOM.ul(null, 
                    React.DOM.li(null, "Open its detailed view"),
                    React.DOM.li(null, "Edit its configuration"),
                    React.DOM.li(null, "Delete it"),
                    React.DOM.li(null, "Download it"),
                    React.DOM.li(null, "Open its capture view"),
                    React.DOM.li(null, "Transfer it to a removable storage device")
                  )
                ),
                React.DOM.p(null, 
                  React.DOM.a( {className:"button", href:"/workflow/new"}, "Create a new workflow")
                ))
              )
          )
        )
      );
    }
  });
}());
