(function() {
  'use strict';
  var React = require('react/addons'),
      jQuery = require('jquery'),
      confirmModal = require('./foundation.js').confirmModal,
      fnLabel = require('./foundation.js').label;

  /**
   * Global navigation bar for the application
   *
   * @property {string} title - Title to display
   */
  module.exports = React.createClass({
    displayName: "NavigationBar",

    getInitialState: function() {
      return {
        /** Display shutdown modal? */
        shutdownModal: false
      };
    },
    /** Initiate shutdown of the hosting machine */
    doShutdown: function() {
      // TODO: Show activity indicator until connection has died
      // TODO: Make UI inactive until polling is successful again
      jQuery.ajax({
        type: "POST",
        url: "/api/system/shutdown"
      });
      this.setState({
        shutdownModal: false
      });
    },
    /** Display shutdown modal to ask user to confirm shutdown */
    handleShutdown: function() {
      this.setState({
        shutdownModal: true
      });
    },
    render: function() {
      return (
        React.DOM.div( {className:"contain-to-grid fixed"}, 
          this.state.shutdownModal &&
            confirmModal(
              {onCancel:function(){this.setState({shutdownModal: false});}.bind(this),
              onConfirm:this.doShutdown, fixed:true}, 
              React.DOM.h1(null, "Shut down"),
              React.DOM.p(null, "Do you really want to shut down the device?"),
              React.DOM.p(null, React.DOM.strong(null, "If you do, please make sure that you turn off your devices before confirming!"))
            ),
          React.DOM.nav( {className:"top-bar", 'data-topbar':true}, 
            React.DOM.ul( {className:"title-area"}, 
              React.DOM.li( {className:"name"},  " ", React.DOM.h1(null, React.DOM.a( {href:"/", title:"Return to workflow list", className:"fi-home"},  " ", this.props.title)), " " ),
              React.DOM.li( {className:"toggle-topbar"}, React.DOM.a( {className:"fi-list"}))
            ),
            React.DOM.section( {className:"top-bar-section"}, 
              React.DOM.ul( {className:"left"}, 
                React.DOM.li(null, React.DOM.a( {href:"/workflow/new"}, React.DOM.i( {className:"fi-plus"}), " New workflow"))
              ),
              React.DOM.ul( {className:"right"}, 
                React.DOM.li(null, 
                  React.DOM.a( {href:"/logging"}, 
                    React.DOM.i( {className:"fi-list"}), " Show log",
                    this.props.numUnreadErrors > 0 &&fnLabel( {level:"alert", round:true},  " ", this.props.numUnreadErrors)
                  )
                ),
                /* Only show shutdown button if the application is running in standalone mode */
                window.config.web.standalone_device &&
                  (React.DOM.li(null, React.DOM.a( {onClick:this.handleShutdown}, React.DOM.i( {className:"fi-power"}), " Shut down")))
              )
            )
          )
        )
      );
    }
  });
}());
